
#p-button{
    max-width:300px;
    width: 80%;
    margin-left: 10%;
    margin-right: 10%;
    margin-top: 20%;
    height: 8vh;
    border: 2px solid rgb(207, 142, 142);
    border-radius: 4vh;
    background-color: rgb(217, 252, 255);
}

.f-div1{
    width: 100%;
    height: 100vh;
    padding-top: 10vh;
    background-image: linear-gradient(to top, #f3e7e9 0%, #e3eeff 99%, #e3eeff 100%);
}

.f-div{
    max-width: 500px;
    width: 80vw;
    height: 80vh;
    margin: auto;
    border-radius: 5vh;
    /* padding-top: 10vh; */
    background-image: radial-gradient( circle farthest-corner at 10% 20%,  rgba(97,186,255,1) 0%, rgba(166,239,253,1) 90.1% );
}


.d-uselist{
    background-image: linear-gradient(to bottom,#92FFC0,#002661);
    min-height: 100vh;
    width:100%;
}

.i-adduser{
    width:80%;
    margin-left: 8%;
}

.user-input{
    border: solid 1px grey;
    margin-left: 10vw;
    width: 80vw;
    height: 10vh;
    border-radius: 5vh;
}

.Newuser{
    background-color:darkslategrey;
    width: 100%;
    height: 15vh;
    text-align: center;
    padding-top: 3vh;
    font-size: 4em;
    color:#eee;
    font-family:monospace;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}

.A-Label{
    color: #eee;
    padding-left: 9%;
    font-size: 1.2em;
}

input[type=radio]{
    transform:scale(1.4);
}

.btn-s{
    width: 40%;
    height: 6vh;
    border-radius: 3vh;
    background-color:aqua ;
    margin-left: 2%;
}